@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* font color #fd4370 */

.home-container{
  width: 100%;
  height: 80vh;
  background: #0e1523;
  color: white;
}
::-webkit-scrollbar {
  width: 20px;
}
::-webkit-scrollbar-track {
  background-color: #0e1523;
}
::-webkit-scrollbar-thumb {
  background-color: #d6dee1;
  border-radius: 20px;
  border: 6px solid transparent;
  background-clip: content-box;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #a8bbbf;
}
ul li{
    display: inline-flex;
    padding: 10px 20px;
    margin-top: 10px;
    font-size: 14px;
    cursor: pointer;
    text-transform: uppercase;
    letter-spacing: 2px;
    transition: .5s;
}

ul li:hover{
    color: #f85a82;
}

.brand{
    color: white;
    margin: 0;
    padding: 0;
}
.container .greet{
    font-size: 60px;
    letter-spacing: 2px;
    font-family: 'Nunito', sans-serif;
    font-weight: 700;
    margin-bottom: 0;
}
.detail-wrapper{
    margin-top: 100px;
}
.container .dev-name{
    color: #fd4370;
}
.container .detail p{
    font-family: 'Nunito', sans-serif;
    font-weight: 200;
    font-size: 20px;
    margin: 0;
}
.person-image{
    opacity: .8;
}
.arrow-down{
    position: absolute;
    bottom: 10%;
    left: 50%;
    -webkit-transform: translate(-10%, -50%);
            transform: translate(-10%, -50%);
}
.arrow-down i{
    font-size: 35px;
    cursor: pointer;
    transition: .5s;
}
.arrow-down i:hover{
    color: #fd4370;
    font-size: 38px;
    text-shadow: 0 0 10px rgb(51, 51, 51);
}
