ul li{
    display: inline-flex;
    padding: 10px 20px;
    margin-top: 10px;
    font-size: 14px;
    cursor: pointer;
    text-transform: uppercase;
    letter-spacing: 2px;
    transition: .5s;
}

ul li:hover{
    color: #f85a82;
}

.brand{
    color: white;
    margin: 0;
    padding: 0;
}